import React, { useState, useEffect, useCallback } from 'react';

const StockTable = () => {
  const [codigo, setCodigo] = useState('');
  const [produto, setProduto] = useState('');
  const [valorVenda, setValorVenda] = useState('');
  const [data, setData] = useState('');
  const [estoque, setEstoque] = useState([]);
  const [vendidos, setVendidos] = useState([]);
  const [totalEstoque, setTotalEstoque] = useState(0);
  const [totalVendido, setTotalVendido] = useState(0);

  const calcularTotalEstoque = useCallback(() => {
    const total = estoque.reduce((acc, item) => acc + parseFloat(item.valorVenda), 0);
    setTotalEstoque(total);
  }, [estoque]);

  const calcularTotalVendido = useCallback(() => {
    const total = vendidos.reduce((acc, item) => acc + parseFloat(item.valorVenda), 0);
    setTotalVendido(total);
  }, [vendidos]);

  useEffect(() => {
    calcularTotalEstoque();
  }, [estoque, calcularTotalEstoque]);

  useEffect(() => {
    calcularTotalVendido();
  }, [vendidos, calcularTotalVendido]);

  const adicionarProduto = () => {
    setEstoque([...estoque, { codigo, produto, valorVenda, data }]);
    setCodigo('');
    setProduto('');
    setValorVenda('');
    setData('');
  };

  const venderProduto = (index) => {
    const produtoVendido = estoque[index];
    setVendidos([...vendidos, produtoVendido]);
    const novoEstoque = estoque.filter((_, i) => i !== index);
    setEstoque(novoEstoque);
  };

  const excluirProduto = (index, lista) => {
    if (lista === 'estoque') {
      const novoEstoque = estoque.filter((_, i) => i !== index);
      setEstoque(novoEstoque);
    } else if (lista === 'vendidos') {
      const novoVendidos = vendidos.filter((_, i) => i !== index);
      setVendidos(novoVendidos);
    }
  };

  return (
    <div>
      <h2>Estoque</h2>
      <div>
        <input
          type="text"
          placeholder="Código"
          value={codigo}
          onChange={(e) => setCodigo(e.target.value)}
        />
        <input
          type="text"
          placeholder="Produto"
          value={produto}
          onChange={(e) => setProduto(e.target.value)}
        />
        <input
          type="text"
          placeholder="Valor da Venda"
          value={valorVenda}
          onChange={(e) => setValorVenda(e.target.value)}
        />
        <input
          type="date"
          placeholder="Data"
          value={data}
          onChange={(e) => setData(e.target.value)}
        />
        <button onClick={adicionarProduto}>Adicionar produto</button>
      </div>
      <h3>Estoque</h3>
      <table>
        <thead>
          <tr>
            <th>Código</th>
            <th>Produto</th>
            <th>Valor da Venda</th>
            <th>Data</th>
            <th>Ação</th>
          </tr>
        </thead>
        <tbody>
          {estoque.map((item, index) => (
            <tr key={index}>
              <td>{item.codigo}</td>
              <td>{item.produto}</td>
              <td>{item.valorVenda}</td>
              <td>{item.data}</td>
              <td>
                <button onClick={() => venderProduto(index)}>Vender</button>
                <button onClick={() => excluirProduto(index, 'estoque')}>Excluir</button>
              </td>
            </tr>
          ))}
        </tbody>
      </table>
      <h3>Produtos Vendidos</h3>
      <table>
        <thead>
          <tr>
            <th>Código</th>
            <th>Produto</th>
            <th>Valor da Venda</th>
            <th>Data</th>
            <th>Ação</th>
          </tr>
        </thead>
        <tbody>
          {vendidos.map((item, index) => (
            <tr key={index}>
              <td>{item.codigo}</td>
              <td>{item.produto}</td>
              <td>{item.valorVenda}</td>
              <td>{item.data}</td>
              <td>
                <button onClick={() => excluirProduto(index, 'vendidos')}>Excluir</button>
              </td>
            </tr>
          ))}
        </tbody>
      </table>
      <h3>Total em Estoque: R$ {totalEstoque.toFixed(2)}</h3>
      <h3>Total Vendido: R$ {totalVendido.toFixed(2)}</h3>
    </div>
  );
};

export default StockTable;
