import React, { useState, useEffect } from 'react';

const MonthlySummary = () => {
  const [produtos, setProdutos] = useState([]);
  const [mesSelecionado, setMesSelecionado] = useState('');
  const [totalEstoque, setTotalEstoque] = useState(0);
  const [totalVendido, setTotalVendido] = useState(0);
  const [meses, setMeses] = useState([]);

  useEffect(() => {
    const produtosSalvos = JSON.parse(localStorage.getItem('estoque')) || [];
    const produtosVendidos = JSON.parse(localStorage.getItem('vendidos')) || [];

    const todosProdutos = [...produtosSalvos, ...produtosVendidos];
    setProdutos(todosProdutos);

    const uniqueMonths = [
      ...new Set(
        todosProdutos.map((produto) => {
          const [ano, mes] = produto.data.split('-');
          return `${ano}-${mes}`;
        })
      ),
    ];
    setMeses(uniqueMonths);
  }, []);

  const handleMesClick = (mes) => {
    setMesSelecionado(mes);

    const produtosMes = produtos.filter((produto) => {
      const [ano, mesProduto] = produto.data.split('-');
      return `${ano}-${mesProduto}` === mes;
    });

    const vendidosMes = produtosMes.filter((produto) => produto.vendido);
    const estoqueMes = produtosMes.filter((produto) => !produto.vendido);

    const totalEstoqueMes = estoqueMes.reduce((acc, produto) => acc + produto.valorVenda, 0);
    const totalVendidoMes = vendidosMes.reduce((acc, produto) => acc + produto.valorVenda, 0);

    setTotalEstoque(totalEstoqueMes);
    setTotalVendido(totalVendidoMes);
  };

  return (
    <div>
      <h2>Resumo Mensal</h2>
      <div>
        {meses.map((mes, index) => (
          <button key={index} onClick={() => handleMesClick(mes)}>
            {mes}
          </button>
        ))}
      </div>
      {mesSelecionado && (
        <div>
          <h3>Resumo de {mesSelecionado}</h3>
          <p>Total em Estoque: R$ {totalEstoque.toFixed(2)}</p>
          <p>Total Vendido: R$ {totalVendido.toFixed(2)}</p>
        </div>
      )}
    </div>
  );
};

export default MonthlySummary;
